import { Component, Event, EventEmitter, h, Prop, State } from '@stencil/core';
import { IDocument } from '../../types/Document';

const timeout = 10000;
@Component({
    tag: 'document-list',
    shadow: true,
    styleUrl: 'document-list.scss',
})
export class DocumentList {
    @Prop() public documents: IDocument[] = [];
    @State() public intervalId;
    @Event() public loadRelatedDocuments: EventEmitter;

    public componentWillLoad() {
        this.loadRelatedDocuments.emit();
        this.intervalId = setInterval(() => {
            this.loadRelatedDocuments.emit();
        }, timeout);
    }

    public disconnectedCallback() {
        clearInterval(this.intervalId);
    }

    public render() {
        if (!this.documents.length) {
            return <empty-state text="No documents were found!" />;
        }

        return [
            <ul class="document-list">
                {this.documents.map(document => {
                    return <document-list-item document={document} />;
                })}
            </ul>,
        ];
    }
}
